*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  
}

.container {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 10px;
}
.form {
  display: flex;
  width: 100%;
}
.input-text {
  flex: 8;
  font-size: 14px;
  padding: 12px 15px;
  border: 1px solid #cecece;
  outline: none;
  font-weight: 400;
  max-width: 80%;
}
.input-text::placeholder {
  color: rgb(189, 183, 183);
  opacity: 0.8;
}
.input-submit {
  flex: 2;
  border: none;
  background: #038203;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 600;
  width: 20%;
  outline: none;
  transition: all .5s;
    &:hover{
      background-color: #fff;
      color: #038203;
      border: 1px solid #038203;
    }
}
.todo-item {
  list-style-type: none;
  padding: 17px 0px;
  border-bottom: 1px solid #eaeaea;
}
.todo-item input {
  margin-right: 15px;
}
.todo-item button {
  background: #d35e0f;
  color: #fff;
  border: 1px solid #d35e0f;
  padding: 3px 7px;
  cursor: pointer;
  float: right;
  outline: none;
  transition: all .5s ;
    &:hover {
      background-color: white;
      color: #d35e0f;
    }
}